.news {
   display: flex;
   flex-direction: column;
   margin: 1.5rem 0;
   padding: 0 1rem;

   .news__h2 {
      margin: 1.2rem 0 8px 0;

      span {
         color: #eb1701;
      }
   }

   .news__h3 {
      margin-bottom: 5px;
      font-weight: 500;
   }

   .news__p {
      font-weight: 500;
      margin-top: 5px;
      font-size: 1.1rem;
      margin: 5px 1.1rem 0 1.1rem;
   }

   .news__p--second {
      margin: 0px;
   }

   img {
      height: 230px;
   }
}
